
import DialogShareTip from '@/components/dialog/dialog-share' 
import DialogVoteTip from '@/components/dialog/dialog-vote' 
import FootedTab from "@/components/footed/footed-tab";
import { wxShare } from "@/tools/wxShare";
import { isPhone, wechatOauth } from "@/tools/common";
import XButton from 'vux/src/components/x-button/index.vue'

import { getLists, getVoteDetail, postVote } from '@/api/api'

export default {
components: {
XButton,
FootedTab,
DialogVoteTip,
DialogShareTip
},
data() {
return {
nowDay: '',
votesCount: 5,
isShowVoteDialog: false,
isShowTipDialog: false,
isShowShareDialog: false,
id: this.$route.query.id,
phone: false,
voteData: { id: 1, type: 'food', display_type: '1', number: '222', name: '1', image_path: '', total_votes_count: '3', description: '', isLoad: false },
}
},
created() {
window.wxShare();
this.phone = isPhone;
this.nowDay = this.getDate()

console.log('', this.nowDay)

this.getVoteDetailApi()
this.onGetVotesCount()
},
methods: {
showLoadTip() {
this.$vux.loading.show({ text: '' })
},
hideLoadTip() {
this.$vux.loading.hide()
},
_showToolTip(text, timeout = 2500) {
this.$vux.toast.text(text)
},
getDate() {
let date = new Date();
let month = date.getMonth() + 1;
let strDate = date.getDate();
if (month >= 1 && month <= 9) {
month = "0" + month;
}
if (strDate >= 0 && strDate <= 9) {
strDate = "0" + strDate;
}
let currentdate = date.getFullYear() + month + strDate;
return currentdate;
},

onCancelVoteDialog() {
this.postVoteApi()
},

onCancelTipDialog() {
this.isShowTipDialog = false
this.isShowVoteDialog = false
this.isShowShareDialog = true
},

onVote() {
this.postVoteApi()
},

jumpBack() {
this.$router.push('/vote')
},

async getVoteDetailApi() {
try {
const { data } = await getVoteDetail({ id: this.id })
this.voteData = data
} catch (e) {
this._showToolTip(e)
}
},
async postVoteApi() {
this.showLoadTip()
try {
this.hideLoadTip()
const { data } = await postVote({ id: this.id })
if (data.status === 'success') {
if (this.votesCount > 1) {
this.votesCount--
this.voteData.total_votes_count++
this.isShowVoteDialog = true
this.isShowTipDialog = false
} else {
this.isShowTipDialog = true
this.isShowVoteDialog = false
}
} else {
this.votesCount = 5
this.isShowTipDialog = true
this.isShowVoteDialog = false
}
this.onSaveVotesCount()
} catch (e) {
this.hideLoadTip()
this._showToolTip(e)
}
},
onGetVotesCount() {
this.votesCount = localStorage.getItem(`jw_votes_count_${this.nowDay}`) || 5
let local = `jw_votes_count_${this.nowDay}`
console.log("", this.votesCount, local)
},
onSaveVotesCount() {
localStorage.setItem(`jw_votes_count_${this.nowDay}`, this.votesCount)
console.log("", this.votesCount)
},
}
}



