import Vue from 'vue'
import qs from 'qs'
import md5 from 'md5'
import axios from "axios"
import {
  randomWord,
  wechatOauth
} from '../tools/common'

import store from '../store' // vuex  store 

window.axios = axios;
window.axios.defaults.timeout = 0;

export async function getDefaultHeaders() {
  let headers = {
    'X-Requested-With': 'XMLHttpRequest',
    'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
  }

  const accessKey = 'I35TnSGoDVQvyTeYXyRt6FZBS1Lx56t6'
  const secretKey = 'f7oDnpwYb3gxycZCfDDdqipnF6xuhFtU'
  const timestamp = Date.parse(new Date()) / 1000
  const echostr = randomWord(false, 8)

  headers['api-access-key'] = accessKey
  headers['api-echostr'] = echostr
  headers['api-timestamp'] = timestamp
  headers['api-signature'] = md5(secretKey + echostr + timestamp)

  // let token = sessionStorage.getItem('userToken')
  // if (!token) {token = sessionStorage.getItem('userToken') }
  // headers['Authorization'] = `Bearer ${token}`

  return headers
}

// 
window.axios.interceptors.request.use(
  async config => {
    // 
    store.commit('updateLoadingStatus', {
      isLoading: true
    })

    process.env.NODE_ENV === 'production' && Object.assign(config.headers, await getDefaultHeaders())

    return config;
  }, error => {
    // 
    store.commit('updateLoadingStatus', {
      isLoading: false
    })
    console.error(":", error);
    return Promise.reject(error);
  })

// 
window.axios.interceptors.response.use(
  response => {
    store.commit('updateLoadingStatus', {
      isLoading: false
    })
    return Promise.resolve(response);
  },
  error => {
    store.commit('updateLoadingStatus', {
      isLoading: false
    })
    let errorMsg
    // 
    if (error && error.response) {
      switch (error.response.status) {
        case 400:
          errorMsg = '(400)';
          break;
        case 401:
          wechatOauth()
          errorMsg = '(401)';
          break;
        case 403:
          errorMsg = '(403)';
          break;
        case 404:
          errorMsg = '(404)';
          break;
        case 408:
          errorMsg = '(408)';
          break;
        case 500:
          errorMsg = '(500)';
          break;
        case 501:
          errorMsg = '(501)';
          break;
        case 502:
          errorMsg = '(502)';
          break;
        case 503:
          errorMsg = '(503)';
          break;
        case 504:
          errorMsg = '(504)';
          break;
        case 505:
          errorMsg = 'HTTP(505)';
          break;
      }
      // return Promise.reject(errorMsg)
    } 
    console.error('errorMsg',error)
    let errorData = {data:errorMsg}
      // return Promise.reject(error.response.data);
    return errorMsg ? Promise.reject(errorData) :  Promise.reject(error.response)
  }
)

// 
function checkStatus(res) {
  // http
  if (res && (res.status <= 400)) {
    return res
    // data return response.data
  } else {
    //     // return res
    console.error(res, '')
    return Promise.reject(res.data)
  }
}

// 
function checkCode (response) {
  // code()
  let error = response.data

  if (error) {
    if (error.errors) {
      let arr = error.errors
      for (let i in arr) {
        error = arr[i][0]
      }
    }  else {
      error = error.message || error.err_msg || error
    }

    console.error(', Error', error, response)
    return Promise.reject(error)
  }
}

export default {
  get: (url, params, config = {}) => {
    return window.axios({
      method: 'get',
      url,
      params, // get 
      withCredentials: true,
      // headers: getDefaultHeaders()
      headers: config.headers || {}
    }).then(res => {
      return checkStatus(res)
    }).catch(err => {
      return checkCode(err)
    })
  },

  post: (url, data, config = {}) => {
    return window.axios({
      method: 'post',
      url,
      data: qs.stringify(data),
      // headers: Object.assign(getDefaultHeaders(), config.headers)
      headers: config.headers || {}
    }).then(res => {
      return checkStatus(res)
    }).catch(err => {
      return checkCode(err)
    })
  },

  upload: (url, data, config = {}) => {
    return window.axios({
      method: 'post',
      url,
      data: data,
      // headers: getDefaultHeaders()
      headers: config.headers || {}
    }).then(res => {
      return checkStatus(res)
    }).catch(err => {
      return checkCode(err)
    })
  },

  put: (url, params, config = {}) => {
    return window.axios({
      method: 'put',
      url,
      data: qs.stringify(params),
      // headers: getDefaultHeaders()
      headers: config.headers || {}
    }).then(res => {
      return checkStatus(res)
    }).catch(err => {
      return checkCode(err)
    })
  },

  del: (url, params, config = {}) => {
    return window.axios({
      method: 'delete',
      url,
      data: qs.stringify(params),
      // headers: getDefaultHeaders()
      headers: config.headers || {}
    }).then(res => {
      return checkStatus(res)
    }).catch(err => {
      return checkCode(err)
    })
  }
}



