
import DialogShareTip from '@/components/dialog/dialog-share' 
import DialogVoteTip from '@/components/dialog/dialog-vote' 
import FootedTab from "@/components/footed/footed-tab";
import ListBox from "@/components/list/list-box"
import { wxShare } from "@/tools/wxShare";
import { isPhone, wechatOauth } from "@/tools/common";
import XButton from 'vux/src/components/x-button/index.vue'

import { getLists, postVote, clearVote } from '@/api/api'

export default {
components: {
XButton,
ListBox,
FootedTab,
DialogShareTip,
DialogVoteTip,
},
data() {
return {
isChangeStatus: false,  //
nowDay: '',
votesCount: 5,
isShowVoteDialog: false,
isShowTipDialog: false,
isShowShareDialog: false,
id: '',
vote: {
icon: '',
rules: '<p>120191612:00AM-11012:00AM</p><p>25</p><p>3</p>'
},
phone: false,
voteListData: {
craft: [
{ id: 1, type: 'food', display_type: '1', number: '222', name: '1', description: '', image_path: '', total_votes_count: '3', isLoad: false },
{ id: 2, type: 'food', display_type: '2', number: '222', name: '2', description: '', image_path: '', total_votes_count: '3', isLoad: false }
],
food: [
{ id: 1, type: 'food', display_type: '1', number: '222', name: '1', description: '', image_path: '', total_votes_count: '3', isLoad: false },
{ id: 2, type: 'food', display_type: '2', number: '222', name: '2', description: '', image_path: '', total_votes_count: '3', isLoad: false }
]
},
currentTab: 0,
currentType: 'food',
currentListData: [],
currentIndex: '',
voteTabs: [{ name: '', type: 'food' }, { name: '', type: 'craft' }, { name: '', type: 'culture' }]
}
},
async created() {
window.wxShare();
this.phone = isPhone;
this.nowDay = this.getDate()

console.log('', this.nowDay)

await this.getListsApi()
this.currentListData = this.voteListData.food
this.onGetVotesCount()
},
methods: {
showLoadTip() {
this.$vux.loading.show({ text: '' })
},
hideLoadTip() {
this.$vux.loading.hide()
},
_showToolTip(text, timeout = 2500) {
this.$vux.toast.text(text)
},
getDate() {
let date = new Date();
let month = date.getMonth() + 1;
let strDate = date.getDate();
if (month >= 1 && month <= 9) {
month = "0" + month;
}
if (strDate >= 0 && strDate <= 9) {
strDate = "0" + strDate;
}
let currentdate = date.getFullYear() + month + strDate;
return currentdate;
},

onCancelVoteDialog() {
this.postVoteApi()
},

onCancelTipDialog() {
this.isShowTipDialog = false
this.isShowVoteDialog = false
this.isShowShareDialog = true
},
jumpVoteDetail(item, index) {
this.$router.push('/vote/detail?id=' + item.id)
},
changeTabs(item, index) {
this.currentTab = index
this.currentType = item.type
this.currentListData = this.voteListData[item.type]
this.getListsApi()
},
onVote(item, index) {
this.id = item.id
this.currentIndex = index
this.postVoteApi()
},
async getListsApi() {
try {
const { data } = await getLists()
this.voteListData = {
craft: data.craft,
food: data.food,
culture: data.culture
}
} catch (e) {
this._showToolTip(e)
}
},
async postVoteApi() {
this.showLoadTip()
try {
this.hideLoadTip()
const { data } = await postVote({ id: this.id })
console.log('', data.status, this.votesCount)
if (data.status === 'success') {
if (this.votesCount > 1) {
this.votesCount--
this.currentListData[this.currentIndex].total_votes_count++
this.isShowVoteDialog = true
this.isShowTipDialog = false
} else {
this.isShowTipDialog = true
this.isShowVoteDialog = false
}
} else {
this.votesCount = 5

this.isShowTipDialog = true
this.isShowVoteDialog = false
}
this.onSaveVotesCount()
} catch (e) {
this.hideLoadTip()
this._showToolTip(e)
}
},
async clearVoteApi() {
try {
await clearVote()
console.log('')
} catch (e) {
this._showToolTip(e)
}
},
onClearLocalStorage() {
localStorage.clear();
},
onClearVote() {
this.clearVoteApi()
this.votesCount = 5
this.onSaveVotesCount()
},
onGetVotesCount() {
let isDate = localStorage.getItem(`jw_votes_count_${this.nowDay}`) != null
this.votesCount = localStorage.getItem(`jw_votes_count_${this.nowDay}`) || 5
let local = `jw_votes_count_${this.nowDay}`
console.log("", this.votesCount, isDate)
},
onSaveVotesCount() {
localStorage.setItem(`jw_votes_count_${this.nowDay}`, this.votesCount)
console.log("", this.votesCount)
},
}
}



