import Vue from 'vue'
import {
getWeixinJsSdkConfig
} from "../api/api";
import {
getRouterLink
} from './common'
import WechatPlugin from 'vux/src/plugins/wechat/index.js'


Vue.use(WechatPlugin); 
const wx = Vue.wechat;


export const weixinJsSDKAuth = new Promise((resolve) => {
let timeout = setTimeout(function() {
console.info("JSSDK 10s , ");
clearTimeout(timeout);
resolve(false);
}, 10000);

wx.ready(_ => {
clearTimeout(timeout);
resolve(true)
});
wx.error(function(res) {
clearTimeout(timeout);
console.error("JSSDK: ", res);
resolve(false);
});

(
window._jssdk_config ?
Promise.resolve(window._jssdk_config) :
(getWeixinJsSdkConfig(['onMenuShareTimeline', 'onMenuShareAppMessage', 'chooseWXPay']))
).then(config => {
console.log('JSSDK: ', config)
wx.config(config || {});
});
})




const wx_share_img = 'http://txh-cdn.96qbhy.com/20180929175605wOdcmdBB.png' 

export const wxShare = (shareTitle, shareDesc, shareLink, shareImg, shareSuccess) => {
let options = {
title: shareTitle || '2018',
desc: shareDesc || '',

link: (shareLink || getRouterLink()).replace(/http:\/\//, 'https://'),
imgUrl: shareImg || wx_share_img,
success: shareSuccess || (_ => void 0)
}

console.log(': ', options)

weixinJsSDKAuth.then(boo => {
if (boo) {
wx.onMenuShareTimeline(options) 
wx.onMenuShareAppMessage(options) 
wx.onMenuShareQQ(options) 
wx.onMenuShareQZone(options) 
}
})
}

window.wxShare = wxShare;



